export const APIS = {
  '/api/product/getList': (req: MockRequest) => productGetList(req.queryString.pageNum, req.queryString.pageSize),
  '/api/project/getList': (req: MockRequest) => projectGetList(),
  '/api/product/getProductDetails': (req: MockRequest) => getProductDetails(),
  '/api/project/getProdsWithVersion': (req: MockRequest) => getProdsWithVersion(),

  '/api/list': (req: MockRequest) => getFakeList(req.queryString.count),
  '/api/notice': () => getNotice(),
  '/api/activities': () => getActivities(),
  'POST /api/auth/refresh': { msg: 'ok', token: 'new-token-by-refresh' },
  '/api/401': () => {
    throw new MockStatusError(401);
  },
  '/api/403': () => {
    throw new MockStatusError(403);
  },
  '/api/404': () => {
    throw new MockStatusError(404);
  },
  '/api/500': () => {
    throw new MockStatusError(500);
  },
};
